blockquote {
	font-weight: 100;
	font-size: 1.3rem;
	max-width: 600px;
	line-height: 1.4;
	position: relative;
	margin: 0;
    padding: .5rem;
    /* padding:20px 20px; */
    margin-top:50px;
}

blockquote:before,
blockquote:after {
	position: absolute;
	color: #f1efe6;
	font-size: 8rem;
	width: 4rem;
	height: 4rem;
}

blockquote:before {
	content: '\201C';
	left: -5rem;
	top: -2rem;
}

blockquote:after {
	content: '\201D';
	right: -5rem;
	bottom: 1rem;
}

